.todo-view {
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: row;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
}

.todo-stat {
    width: 200px;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.todo-stat-title {
    width: calc(100% - 5px);
    height: 40px;
    line-height: 35px;
    padding-left: 5px;
    font-size: var(--font-size-mid);
    font-weight: bold;
    color: var(--primary-color-1);
}

.todo-stat-content {
    width: 100%;
    height: calc(100% - 40px - 50px);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-big-1);
    font-weight: bold;
    color: var(--primary-color-3);
}

.todo-stat-bottom {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;
    cursor: pointer;
}

.todo-stat-ok-title {
    font-weight: bold;
    color: var(--primary-color-1);
}


.todo-stat-ok-number {
    font-weight: bold;
    color: var(--primary-color-2);
}

.todo-list {
    width: calc(100% - 200px - 10px);
    height: calc(100% - 10px);
    overflow-y: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 5px;
    padding: 5px;
}

.todo-list-item {
    flex-shrink: 0;
    background: var(--primary-color-3);
    width: 50px;
    height: 80%;
    display: flex;
    flex-direction: column;
    padding: 5px;
    border-radius: var(--border-radius-1);
}

.todo-title {
    width: 100%;
    height: calc(100% - 50px);
    text-align: center;
}

.todo-time {
    width: 100%;
    height: 25px;
    line-height: 25px;
    text-align: center;
    color: var(--primary-color-2);
}

.todo-operate {
    width: 100%;
    height: 25px;
    line-height: 25px;
    text-align: center;
    font-size: var(--font-size-small);
    margin-right: 5px;
    cursor: pointer;
    background: var(--primary-color-2);
    border-radius: var(--border-radius-1);
    color: white;
}
