.data-source {
    height: 100%;
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.data-source-main {
    position: fixed;
    top: 100vh;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    transition: 0.3s;
    border-top-left-radius: var(--border-radius-1);
    border-top-right-radius: var(--border-radius-1);
    display: flex;
    flex-direction: column;
    z-index: var(--zindex-mid);
}

.data-source-main.open {
    top: 0;
    box-shadow: var(--normal-box-shadow);
    border-top: 5px solid var(--primary-color-3);
    background: var(--main-bg-glass-color-1);
    backdrop-filter: var(--main-filter);
}

.data-source-main-title {
    line-height: 80px;
    height: 80px;
    width: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
    text-align: center;
}

.data-source-main-form {
    overflow-y: auto;
    height: calc(100% - 80px);
    width: 100%;
    overflow: auto;
    padding-bottom: 120px;
}

.data-source-bottom {
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    z-index: var(--zindex-max);
}

.data-source-bottom-btn {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: var(--primary-color-1);
    backdrop-filter: var(--main-filter);
    font-size: 25px;
    font-weight: bold;
    border: 5px solid var(--primary-color-3);
    color: var(--primary-color-2);
    flex-direction: column;
    cursor: pointer;
    box-shadow: var(--normal-box-shadow);
}

.data-source-bottom-btn {
    box-shadow: var(--normal-box-shadow);
}

