.data-type-view {
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: var(--main-text-color-1);
}

.data-type-view-item {
    height: 100%;
    width: 100%;
}

.data-type-view-item-title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: var(--font-size-big);
    font-weight: bold;
}

.data-type-view-item-main {
    height: calc(100% - 50px);
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.data-type-app-view-item {
    width: 100%;
}

.timer {
    height: 100px;
    width: calc(100% - 20px);
    display: flex;
    flex-direction: row;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
    color: white;
    padding: 5px;
    margin: 5px;
}

.timeroperate {
    width: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 10px;
}

.timeroperate-icon {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    font-size: var(--font-size-small);
    font-weight: bold;
    color: white;
    border: 5px solid var(--primary-color-3);
}

.timerinfo {
    width: calc(100% - 150px);
    height: 100%;
    font-size: var(--font-size-big);
}