/*layout*/
.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.sys-head {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    z-index: var(--zindex-mid);
    background: var(--main-bg-glass-color-1);
    /*background: var(--main-bg-color-1);*/
    backdrop-filter: var(--main-filter);
    display: flex;
    flex-direction: row;
    /*border-bottom: 1px solid var(--main-text-glass-color-1);*/
}

.sys-head-left {
    width: calc(30% - 10px);
    height: 100%;
    padding-left: 10px;
    font-size: 25px;
    font-weight: bold;
    text-align: left;
    color: var(--primary-color-1);
}

.sys-head-mid {
    width: 40%;
    height: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
    text-align: center;
}

.sys-head-right {
    width: calc(30% - 10px);
    height: 100%;
    font-size: 25px;
    text-align: right;
    padding-right: 10px;
    color: var(--primary-color-1);
}

.sys-head-right .btn {
    font-size: var(--font-size-small);
    margin-right: 5px;
    cursor: pointer;
}

.sys-main {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: auto;
    padding-top: 50px;
    background: var(--natrure-bg);
    background: var(--main-bg-glass-color-1);
}

.sys-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 120px;
    width: 100vw;
    z-index: var(--zindex-mid);
}

/*msg*/
.sys-msg-container {
    width: 100%;
    position: fixed;
    top: 10px;
    z-index: var(--zindex-max);
}

.sys-msg {
    width: 10px;
    height: 10px;
    border-radius: 30px;
    transition: 0.5s;
    background: black;
    border: 1px solid var(--main-text-color-1);
    box-shadow: var(--normal-box-shadow);
    opacity: 0;
}


.sys-msg.toast {
    opacity: 1;
    width: 130px;
    height: 130px;
}

.sys-msg.small {
    opacity: 1;
    width: 50vw;
    height: 30px;
}

.sys-msg.mid {
    opacity: 1;
    width: calc(100vw - 20px);
    height: 90px;
    border-radius: 35px;
}

.sys-msg.big {
    opacity: 1;
    width: calc(100vw - 20px);
    height: 150px;
}

.sys-msg-content {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: 0.5s;
}

.msg-icon {
    width: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
    color: var(--success-color);
    text-align: center;
    margin-bottom: 5px;
}

.msg-text {
    width: 100%;
    font-size: var(--font-size-small);
    text-align: center;
    color: white;
}

/*form*/
.input {
    width: calc(100% - 20px);
    padding: 10px;
    height: 100px;
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.input-title {
    height: 40px;
    width: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.input-content {
    height: 50%;
    width: 100%;
}

.input-content input {
    width: calc(100% - 10px - 10px);
    height: calc(100% - 10px);
    outline-style: none;
    background: var(--primary-color-3);
    border: 5px solid var(--primary-color-1);
    padding: 5px 5px;
    font-size: var(--font-size-mid);
    color: var(--main-text-color-1);
}

.input-content input:focus {
    box-shadow: var(--normal-box-shadow);
}

.input-content select {
    width: 100%;
    height: 100%;
    outline-style: none;
    background: var(--primary-color-3);
    border: 5px solid var(--primary-color-1);
    font-size: var(--font-size-mid);
    color: var(--main-text-color-1);
}

.sys-empty {
    width: 100%;
    height: 100px;
    font-size: var(--font-size-small);
    color: grey;
}

@media screen and (max-width: 768px) {
    ::-webkit-scrollbar {
        display: none;
    }
}

/*animate*/
.dh-shine {
    animation: shine 1s infinite ease-in-out;
}

@keyframes shine {
    from {
        opacity: 0;
    }
    to {
        opacity: 100;
    }
}