.timer-view-list {
    display: flex;
    flex-direction: row;
    overflow: auto;
    gap: 10px;
}

.timer-view {
    width: 330px;
    height: 80px;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
}

.timer-operate {
    width: 150px;
    height: 100%;
    color: var(--primary-color-3);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 10px;
}

.timer-operate .btn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-size: var(--font-size-mid);
    color: white;
    font-weight: bold;
}

.timer-title {
    height: calc(100% - 10px);
    width: 100px;
    font-size: var(--font-size-mid);
    font-weight: bold;
    color: var(--primary-color-2);
    display: flex;
    justify-content: center;
    align-items: end;
    padding-bottom: 10px;
}

.timer-time {
    padding-left: 5px;
    padding-right: 5px;
    height: 100%;
    width: calc(100% - 200px - 10px);
    font-size: var(--font-size-mid);
    color: var(--primary-color-2);
}

.count-second {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 200;
    backdrop-filter: var(--main-filter);
    display: flex;
    flex-direction: column;
}

.count-second-header {
    height: 60px;
    width: 100%;
    font-size: var(--font-size-big);
}

.count-second-main {
    height: calc(100% - 60px - 60px);
    width: 100%;
    font-size: var(--font-size-big-1);
    font-weight: bold;
    color: var(--primary-color-2);
}

.count-second-bottom {
    height: 60px;
    width: 100%;
    font-size: var(--font-size-mid);
    cursor: pointer;
}
