:root {
    --main-bg-color-1: rgb(237, 237, 237);
    --main-bg-glass-color-1: rgba(237, 237, 237, 0.8);
    --main-bg-color-2: rgba(251, 251, 253);
    --main-text-color-1: rgba(0, 0, 0);
    --main-text-glass-color-1: rgba(0, 0, 0, 0.2);
    --main-text-color-2: rgba(22, 22, 23);
    --primary-color-1: #790ce7;
    --primary-color-2: #f95b1a;
    --primary-color-3: #fec600;
    --natrure-bg: linear-gradient(0deg, rgb(211, 250, 253) 0%, var(--natrure-head-bg) 100%);
    --natrure-head-bg: rgba(99, 171, 221);
    --natrure-head-glass-bg: rgba(99, 171, 221, 0.9);
    /*common*/
    --main-filter: saturate(180%) blur(20px) !important;
    --normal-box-shadow: 0 2px 12px 0 var(--main-bg-glass-color-1);
    --border-radius-1: 10px;
    --border-radius-2: 25px;
    --zindex-low: 0;
    --zindex-mid: 50;
    --zindex-max: 100;
    --font-size-min: 10px;
    --font-size-small: 16px;
    --font-size-mid: 20px;
    --font-size-big: 30px;
    --font-size-big-1: 50px;
    --font-size-big-2: 80px;
    --success-color: lightgreen;
    --warn-color: yellow;
    --error-color: red;
    --info-color: var(--main-text-color-1);
}

:root[theme="dark"] {
    --main-bg-color-1: rgba(0, 0, 0);
    --main-bg-glass-color-1: rgba(0, 0, 0, 0.8);
    --main-bg-color-2: rgba(22, 22, 23);
    --main-text-color-1: rgba(245, 245, 247);
    --main-text-glass-color-1: rgba(245, 245, 247, 0.2);
    --main-text-color-2: rgba(251, 251, 253);
    --natrure-bg: linear-gradient(0deg, rgba(25, 50, 80, 1) 0%, var(--natrure-head-bg) 100%);
    --natrure-head-bg: rgba(4, 6, 22);
    --natrure-head-glass-bg: rgba(4, 6, 22, 0.8);
}

body {
    background: var(--main-bg-color-1);
    color: var(--main-text-color-1);
}